.main-btn {
  transition: all 0.4s;
  display: flex;
  align-items: center;
  width: fit-content;
  border-radius: 48px;
  backdrop-filter: blur(38.49275588989258px);
  box-shadow: inset 1px 1px 1px 0 rgba(255, 255, 255, 0.3);
  background: var(--green);
  min-width: clamp(190px, 16.338vw, 232px);
  box-shadow: 1.203px 1.203px 1.203px 0px rgba(255, 255, 255, 0.3) inset;
  backdrop-filter: blur(19.24637794494629px);

  transition: all 0.3s;
  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: "";
    border: 1px solid var(--green);
    inset: 0;
    border-radius: inherit;
    transition: all 0.3s;
    pointer-events: none;
    opacity: 0;
  }

  @include desktop {
    @include for-mobile {
      min-width: clamp(135px, 13.3803vw, 190px);
    }
  }

  span:not([class]) {
    color: var(--white);
    text-align: center;
    font-family: var(--font-family);
    font-size: clamp(16px, 1.338vw, 19px);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    flex-grow: 1;
    transition: all 0.3s;

    @include desktop {
      @include for-mobile {
        font-size: clamp(11px, 1.1268vw, 16px);
      }
    }

    @include mobile {
      font-size: 16px;
    }
  }

  padding: 6px;

  @include desktop {
    padding: 5px;
  }

  @include tablet {
    padding: 4px;
  }

  @include mobile {
    padding: 5px;
  }

  &__icon {
    width: clamp(45px, 3.3803vw, 48px);
    height: clamp(45px, 3.3803vw, 48px);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    transition: all 0.3s;
    background: var(--white);
    color: var(--green-2);

    @include desktop {
      @include for-mobile {
        width: clamp(31px, 3.169vw, 45px);
        height: clamp(31px, 3.169vw, 45px);
      }
    }

    svg,
    img {
      display: flex;
      width: clamp(17px, 1.4085vw, 20px);
      height: auto;

      @include desktop {
        @include for-mobile {
          width: clamp(12px, 1.4085vw, 17px);
        }
      }

      @include mobile {
        width: 13px;
      }
    }
  }

  &:hover {
    background: transparent;
    backdrop-filter: blur(40px);
    span {
      color: var(--green);
    }
    &::before {
      opacity: 1;
    }

    .main-btn__icon {
      background: var(--green-2);
      color: var(--white);
    }
  }

  &--transparent {
    background: rgba(255, 255, 255, 0.2);

    padding: 3px 6px;

    @include desktop {
      padding: 5px 8px;
    }

    @include tablet {
      padding: 3px 5px;
    }

    @include mobile {
      padding: 5px;
    }

    .main-btn {
      &__icon {
        background: var(--white);
        color: var(--green-2);
        width: clamp(45px, 3.8028vw, 54px);
        height: clamp(45px, 3.8028vw, 54px);

        @include desktop {
          @include for-mobile {
            width: clamp(32px, 3.169vw, 45px);
            height: clamp(32px, 3.169vw, 45px);
          }
        }

        svg,
        img {
          width: clamp(20px, 1.6901vw, 24px);

          @include desktop {
            @include for-mobile {
              width: clamp(14px, 1.4085vw, 20px);
            }
          }
        }
      }

      &:hover {
        .main-btn__icon {
          background: var(--green-2);
          color: var(--white);
        }
      }
    }
  }
}

.swiper-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  aspect-ratio: 1;
  border-radius: 100%;
  transition: all 0.3s;
  width: 24px;

  @include desktop{
    @include for-tablet{
      width: clamp(14px, 1.4085vw, 20px);
    }
  }

  @include tablet{
    width: 14px;
  }

  svg,
  img {
    width: 100%;
    height: 100%;
  }

  &.next {
    svg,
    img {
      transform: scaleX(-1);
    }
  }

  &:hover {
    opacity: 0.8;
  }

  &.swiper-button-disabled {
    opacity: 0.4;
  }
}

.swiper-pagination {
  display: flex;
  align-items: center;
  grid-gap: 20px;

  &-bullet {
    border-radius: 100%;
    display: block;
    transition: all 0.3s;
    cursor: pointer;
  }
}

