.default-btn {
  width: fit-content;
  min-height: 49px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--blue);
  border: 1px solid var(--br-e);
  border-radius: 100px;
  font-size: 14px;
  line-height: 173%;
  font-weight: 600;
  color: var(--cl-a);
  transition: all 0.3s ease;

  svg {
    fill: transparent;
    stroke: currentColor;
  }

  &:hover {
    background-color: var(--white);
    color: var(--cl-c);
  }

  &--deposite {
    max-width: 185px;
    width: 100%;
  }
}
